// 债权转让及受让协议
import { getHeaderIndex } from "../utils/sheet";
export default function loan(app, INFO, str) {
  [
    ["协议编号", getHeaderIndex("借款协议编号")],
    ["借款人名称", getHeaderIndex("借款人")],
    [
      "证件类型",
      () => {
        return !INFO.isEnsure
          ? getHeaderIndex("借款人证件类型")
          : getHeaderIndex("担保人证件类型");
      }
    ],
    [
      "证件号码",
      () => {
        return !INFO.isEnsure
          ? getHeaderIndex("借款人证件号码")
          : getHeaderIndex("担保人证件号码");
      }
    ],
    [
      "担保人：",
      () => {
        INFO.isEnsure = true;
        return getHeaderIndex("担保人");
      }
    ],
    ["乙方出借金额：", getHeaderIndex("借款本金金额")],
    ["约定年化借款利率：", getHeaderIndex("约定年化借款利率")],
    ["借款期限：", getHeaderIndex("借款期限")],
    ["乙方出借日期：", getHeaderIndex("出借日期")],
    ["借款起息日：",getHeaderIndex("借款起息日")],
    ["借款到期日：",getHeaderIndex("借款到期日")],
    ["借款用途：", getHeaderIndex("借款用途")],
    ["还款方式：", getHeaderIndex("还款方式")]
  ].some(keyArr => app._extractInfoByKeyObj(keyArr, str));
}
