// 债权转让及受让协议
import { getHeaderIndex } from "../utils/sheet";
export default function loan(app, INFO, str) {
  INFO[getHeaderIndex("借款人")] = "非借款协议，无借款人信息";
  [
    ["协议编号", getHeaderIndex("借款协议编号")],
    // ["服务方（以下简称“乙方”）", 2],
    [
      "授权出借本金：",
      getHeaderIndex("借款本金金额"),
      {
        transformData(index, str) {
          // debugger;
          let s = str.substr(str.indexOf("授权出借本金：") + 7);
          s = s.substr(0, s.indexOf("元"));
          return [index, s];
        }
      }
    ],
    ["历史参考年化利率：", getHeaderIndex("约定年化借款利率")],
    ["2.2 授权服务期限：", getHeaderIndex("借款期限")],
    ["服务起始日：", getHeaderIndex("出借日期")],
    ["服务结束日：", getHeaderIndex("借款到期日")]
  ].some(keyArr => app._extractInfoByKeyObj(keyArr, str));
}
