<template>
  <div>
    <c-header @change="changeMenu"></c-header>
    <c-download v-show="menuIndex===0"></c-download>
    <div v-show="menuIndex===1" class="container-fluid">
      <div class="card card-main" style="width:30rem;">
        <div class="card-body">
          <h5 class="card-title">懒投资债权整理工具</h5>
          <p class="card-text">将所有PDF格式的出借合同提取信息整理成一份表格</p>
          <div class="alert alert-warning" role="alert">
            该程序并非上传文件后操作，所有操作均为本地磁盘进行，无需担心隐私会被泄漏。如果有疑虑，请停止使用该程序。<br>
            关于excel合计金额与APP显示的待收本金存在差异，有可能是合同类型未识别、懒投资手续费等差异。
            如还有疑问或未支持的合同格式可以联系 ltzactivist@outlook.com
          </div>

          <div class="alert alert-danger" role="alert">
            <strong>新增借款人统计功能，由 <a href="http://ltz.peter-lab.com/">peter-lab</a>提供。出借人在分析完合同后可自行选择是否上报。</strong>
          </div>
          <div class="alert alert-danger" role="alert">
            如果无法使用，请使用 <a href="https://www.google.cn/chrome/">最新版Chrome浏览器</a>
            乱码问题：使用Chrome浏览器，如果office是比较旧的版本，使用 wps 。
          </div>

          <div style="margin-top:50px" class="progress">
            <div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" aria-valuenow="75" aria-valuemin="0" aria-valuemax="100" :style="{width:progressValue+'%'}"></div>
          </div>

          <div style="margin-top:50px" v-show="err.length" class="alert alert-danger" role="alert">
            <p v-for="(e,index) in err" :key="index">{{e}}</p>
          </div>

          <div class="progress-text">
            {{progressText}}
          </div>

          <div class="button-wrap">
            <button @click="onClickButton" type="button" class="btn btn-primary btn-block">选择文件(PDF格式)</button>
            <input multiple ref="fileInput" @change="onChangeInputFile" class="file-input" accept="application/pdf" type="file" id="files" />
          </div>
          <div v-show="isDownload" class="button-wrap">
            <a :href="downloadData" class="btn btn-success btn-block" download="懒投资债权整理.csv">点击下载</a>
            <button style="margin-top:15px" @click="showUploadDataModal" class="btn btn-primary btn-block">上报出借人资料</button>
          </div>
        </div>
      </div>
    </div>

    <!-- toasts -->
    <div style="position: fixed; top: 20px; right: 20px;width:400px" role="alert" aria-live="assertive" aria-atomic="true" class="toast fade hide" data-delay="5000" data-autohide="true">
      <div class="toast-header">
        <svg class="bd-placeholder-img rounded mr-2" width="20" height="20" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img">
          <rect width="100%" height="100%" fill="#007aff"></rect>
        </svg>
        <strong class="mr-auto">信息提醒</strong>
        <button type="button" class="ml-2 mb-1 close" data-dismiss="toast" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="toast-body">
        {{toastInfo}}
      </div>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">是否将借款人信息上报？</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>该功能上报仅用作统计借款人、担保人的信息统计，不为用作其他用途。</p>
            <p>如果需要查看统计后的数据，可访问：<a target="_blank" href="http://ltz.peter-lab.com/">http://ltz.peter-lab.com/</a></p>
            <form method="POST" target="uploadFrame" action="http://ltz.peter-lab.com/upload" id="uploadForm" class="">
              <div class="form-group">
                <label for="exampleInputEmail1">邮箱地址</label>
                <input name="username" required v-model="uploadUserName" type="text" class="form-control required" id="exampleInputEmail1" :class="{'is-invalid':!userNameInvalid,'is-valid':userNameInvalid}" aria-describedby="emailHelp">
                <small id="emailHelp" class="form-text text-muted">填写你的常用邮箱地址，用于唯一的统计标识和联系方式。</small>
                <small v-show="!userNameInvalid" class="invalid-feedback">请填写正确的邮箱地址。</small>
              </div>
              <input name="data" id="uploadDataInput" type="hidden">
            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">关闭</button>
            <button @click="uploadToPeterLab" type="button" class="btn btn-primary">同意上报</button>
          </div>
        </div>
      </div>
    </div>
    <iframe @load="formSubmited" style="width:0px;height:0px;border:none" name="uploadFrame" id="uploadFrame"></iframe>
  </div>
</template>

<script>
var pdfjsLib = window["pdfjs-dist/build/pdf"];
// pdfjsLib.cMapUrl = "//mozilla/pdfjs-dist/tree/master/cmaps";
// PDFJS.cMapPacked = true;
// The workerSrc property shall be specified.
pdfjsLib.GlobalWorkerOptions.workerSrc =
  "https://cdn.staticfile.org/pdf.js/2.3.200/pdf.worker.min.js";

let SheetDatas = [SheetHeader.join(",")];
// const CSVContent = "data:text/csv;charset=utf-8,\ufeff";

import { SheetHeader, putHeaderIndex, getHeaderIndex } from "./utils/sheet";
import loanParser from "./parser/loan";
import assignParser from "./parser/assign";
import agentParser from "./parser/agent";
import redeliveryParser from "./parser/redelivery";
import zhitouParser from "./parser/zhitou";
import tripartiteParser from "./parser/tripartite";

import header from "./components/header";
import download from "./components/download";

// import axios from "axios";

const Parsers = {
  loan: loanParser,
  redelivery: redeliveryParser,
  assign: assignParser,
  agent: agentParser,
  zhitou: zhitouParser,
  tripartite: tripartiteParser
};

function putValue(INFO, index, str, division = "：") {
  let strArray = str.split(division);

  if (strArray.length < 2) {
    return;
  }
  let value = strArray[1].trim();
  if (~value.indexOf(",")) {
    value = JSON.stringify(value);
  }

  if (index === getHeaderIndex("借款本金金额")) {
    value = value.replace("元", "");
  }

  INFO[index] = value;
}

function log(...msg) {
  if (process.env.NODE_ENV === "development") {
    console.log.apply(console, msg);
  }
}

export default {
  name: "app",
  data: () => ({
    isDownload: false,
    downloadData: "",
    progressValue: 0,
    progressText: "请选择文件",
    err: [],
    menuIndex: 0,
    uploadUserName: "",
    toastInfo: ""
  }),
  methods: {
    formSubmited() {
      if (!this.formSubmitedFlag) return;
      this.toastInfo = "上报成功";
      alert('出借人资料上报成功')
      this.progressText = "上报成功，点击下载文件";
      jQuery(".toast").toast("show");
    },
    showUploadDataModal() {
      window.jQuery("#exampleModal").modal("show");
    },
    changeMenu(index) {
      this.menuIndex = index;
    },
    uploadToPeterLab() {
      if (!this.userNameInvalid) return;
      this.formSubmitedFlag = false;
      var reader = new FileReader();
      reader.onload = e => {
        let data = e.target.result;
        if (!data) return;
        this.progressText = "上报数据中，请勿关闭浏览器";
        data = data.replace(/^data:text\/csv;charset=utf-8;base64,/, "");
        jQuery("#uploadDataInput").val(data);
        jQuery("#uploadForm").submit();
        this.formSubmitedFlag = true;
        window.jQuery("#exampleModal").modal("hide");
      };
      reader.readAsDataURL(this.csvBlobData);
    },
    _extractPutInfo(match, index, str, option = {}) {
      if (typeof match === "string") {
        if (~str.indexOf(match)) {
          if (typeof index === "function") {
            index = index();
          }
          if (option.transformData) {
            [index, str] = option.transformData(index, str);
          }
          putValue(this.INFO, index, str, option.division);
          return true;
        }
      }
    },
    _extractInfoByKeyObj([match, index, option = {}], str) {
      return this._extractPutInfo(match, index, str, option);
    },
    _getContractType(str) {
      switch (str) {
        case "借款协议":
          return "loan";
        case "债权转让及受让协议":
          return "assign";
        case "委托投资合同":
          return "agent";
        case "智选服务预约复投服务协议":
        case "智选服务协议":
          return "redelivery";
        case "融资借款合同":
          return "zhitou";
        case "三方借款合同":
          return "tripartite";
        default:
          return void 0;
      }
    },
    extractInfo({ str }, index) {
      let contractType;
      if (index === 0 && (contractType = this._getContractType(str))) {
        this.INFO.type = contractType;
        putHeaderIndex(this.INFO, "协议类型", str);
        return;
      }

      if (~str.indexOf("发生展期")) {
        putHeaderIndex(this.INFO, "是否含有展期补充协议", "是");
      }

      if (!this.INFO.type) {
        throw new Error("unkonwType:未知合同类型");
      }

      let parser = Parsers[this.INFO.type];
      parser(this, this.INFO, str);
    },
    _renderPdfPage(pdf, pageNum, maxPages) {
      return new Promise((resolve, reject) => {
        let page = pdf.getPage(pageNum);
        page.then(page => {
          let textContent = page.getTextContent();
          textContent.then(text => {
            try {
              text.items.map(this._extractInfo);
              if (pageNum < maxPages) {
                this._renderPdfPage(pdf, pageNum + 1, maxPages).then(
                  resolve,
                  reject
                );
              } else {
                resolve();
              }
            } catch (err) {
              putHeaderIndex(
                this.INFO,
                "借款协议编号",
                `${this._currentFile.name} 解释失败，原因：${err.message}`
              );
              this.printError(this.INFO[getHeaderIndex("借款协议编号")]);
              console.error(err);
              resolve();
            }
          });
        });
      });
    },
    renderPdf(pdfData, file) {
      return new Promise(resolve => {
        this.progressText = `加载资源…`;
        var loadingTask = pdfjsLib.getDocument({
          data: pdfData,
          cMapUrl: "https://mozilla.github.io/pdf.js/web/cmaps/",
          cMapPacked: true
        });
        log(`解释文件：${file.name}`);
        this.progressText = `解释文件：${file.name}`;
        loadingTask.promise.then(
          pdf => {
            let maxPages = pdf._pdfInfo.numPages;
            this._renderPdfPage(pdf, 1, maxPages)
              .then(resolve)
              .then(() => {
                pdf.destroy();
              });
          },
          function(reason) {
            // PDF loading error
            alert(reason);
            console.error(reason);
          }
        );
      });
    },
    toBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(new Uint8Array(reader.result));
        reader.onerror = error => reject(error);
        reader.readAsArrayBuffer(file);
      });
    },
    async toTransform(file) {
      try {
        let data = await this.toBase64(file);
        return this.renderPdf(data, file);
      } catch (err) {
        console.error(err);
        this.progressText = err.message;
      }
    },
    async loopFilesTransform(index = 0) {
      this.INFO = new Array(18);
      putHeaderIndex(this.INFO, "债权序号", index + 1);
      this._currentFile = this.files[index];

      let filename = this._currentFile.name;
      putHeaderIndex(this.INFO, "来源文件名", filename);

      let nameInfos = filename.substr(0, filename.indexOf(".")).split("-");
      if (nameInfos.length >= 3) {
        putHeaderIndex(this.INFO, "项目名称", nameInfos[0]);
        putHeaderIndex(this.INFO, "订单编号", nameInfos[1]);
        putHeaderIndex(this.INFO, "债权编号", nameInfos[2]);
      }

      await this.toTransform(this._currentFile);
      SheetDatas.push(this.INFO.join(","));
      index++;
      this.progressValue = Math.ceil((index / this.filesLen) * 100);
      log(this.INFO);
      if (index < this.filesLen) {
        return this.loopFilesTransform(index);
      }
    },
    async onChangeInputFile() {
      this.err = [];
      this.isDownload = false;
      this.downloadData = "";
      SheetDatas.length = 1;
      let files = this.$refs.fileInput.files;
      this.files = files;
      this.filesLen = files.length;
      await this.loopFilesTransform();
      // this.progressValue = 0;
      this.progressText = "点击下载文件";

      var csvData = new Blob(["\ufeff", SheetDatas.join("\r\n")], {
        type: "text/csv;charset=utf-8"
      }); //new way
      var csvUrl = URL.createObjectURL(csvData);
      this.csvBlobData = csvData;
      this.downloadData = csvUrl;
      this.isDownload = true;
      window.jQuery("#exampleModal").modal("show");
    },
    onClickButton() {
      document.getElementById("files").click();
    },
    printError(errMsg) {
      this.err.push(errMsg);
    }
  },
  computed: {
    userNameInvalid() {
      return /^(([^<>()\\[\]\\.,;:\s@"]+(\.[^<>()\\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
        this.uploadUserName
      );
    }
  },
  mounted() {
    // window.jQuery("#exampleModal").modal("show");
  },
  created() {
    this._extractInfo = this.extractInfo.bind(this);
    if (window.location.hash === "#export") {
      this.menuIndex = 1;
    }
  },
  components: {
    cHeader: header,
    cDownload: download
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.card-main {
  margin: 2% auto;
}

.button-wrap {
  position: relative;
  margin-top: 10px;
}

.file-input {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
}
.progress-text {
  text-align: right;
  font-size: 14px;
  color: #aaa;
}
</style>
