// 债权转让及受让协议
import { getHeaderIndex } from "../utils/sheet";

export default function loan(app, INFO, str) {
  [
    [
      "合同编号:",
      getHeaderIndex("借款协议编号"),
      {
        division: ":"
      }
    ],
    ["受托人名称：", getHeaderIndex("借款人")],
    [
      "受托人法定代表人：",
      () => {
        return !app.INFO.isEnsure
          ? getHeaderIndex("借款人证件类型")
          : getHeaderIndex("担保人证件类型");
      }
    ],
    [
      "受托人统一社会信用代码：",
      () => {
        return !app.INFO.isEnsure
          ? getHeaderIndex("借款人证件号码")
          : getHeaderIndex("担保人证件号码");
      }
    ],
    ["还本付息方式：", getHeaderIndex("还款方式")]
  ].some(keyArr => app._extractInfoByKeyObj(keyArr, str));

  if (~str.indexOf("委托资金金额及支付日：")) {
    let s = str.substr(str.indexOf("：") + 1).split("，");
    app._extractPutInfo(
      "委托资金金额为：",
      getHeaderIndex("借款本金金额"),
      s[0]
    );
    app._extractPutInfo(
      "支付日期：",
      getHeaderIndex("出借日期"),
      s[1].replace("。", "")
    );
  }
  if (~str.indexOf("标的资产投资期限：")) {
    let s = str.split("，")[0];
    app._extractPutInfo("标的资产投资期限：", getHeaderIndex("借款期限"), s);
  }
  if (~str.indexOf("标的资产回购利息：")) {
    let s = str.split("，")[0];
    s = s.replace(/年化/g, "");
    app._extractPutInfo(
      "标的资产回购利息：",
      getHeaderIndex("约定年化借款利率"),
      s
    );
  }
}
